.popper-base {
  width: 100%;
  min-width: 223px;
  max-height: min((100vh - 96px) - 60px, 734px);
  min-height: 100px;
  padding: 8px 0;
  display: flex;
  flex-direction: column;
  background: rgb(255, 255, 255);
  box-shadow: rgb(0 0 0 / 12%) 0px 2px 12px;
  border-radius: 8px;
  box-sizing: border-box;
}
