.wrapper {
  position: fixed;
  inset: 0px;
  z-index: 1001;
  overflow: auto;
  outline: 0px;
  transition: all 500ms cubic-bezier(0.075, 0.82, 0.165, 1) 0s;
  transition-duration: 500ms;
  transition-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);
  transition-delay: 0s;
  transition-property: all;
  display: flex;
  opacity: 1;
}
